import React from 'react'
import Helmet from 'react-helmet'
import config from '../utils/siteConfig'
import Layout from '../components/Layout'
import Container from '../components/Container'
import PageTitle from '../components/PageTitle'
import ContactForm from '../components/ContactForm'
import SEO from '../components/SEO'
import styled from 'styled-components'

const Contact = ({ data }) => {
  const postNode = {
    title: `Contact - ${config.siteTitle}`,
  }

  const Wrapper = styled.div`
    padding: 5em;
    text-align: center;
    margin: auto;
  `;

  return (
    <Layout>
      <Helmet>
        <title>{`Contact - ${config.siteTitle}`}</title>
      </Helmet>
      <SEO postNode={postNode} pagePath="contact" customTitle />

      <Container>
        <Wrapper>
        <iframe
              src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d1523.305764337594!2d28.9328099253253!3d40.21770348183601!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x14ca115c27552a0b%3A0x4a3f51ec5675eccf!2sAlt%C4%B1n%C5%9Fehir+Mahallesi%2C+205.+Sk.+No%3A38%2C+16120+Nil%C3%BCfer%2FBursa!5e0!3m2!1sen!2str!4v1535974107440"
              width="600"
              height="450"
              frameBorder="0"
              allowFullScreen
          />
          <address>
            Telefon Numaramız: <a href="tel:0543-220-18-16">0543 220 18 16</a><br/>
          </address>

        </Wrapper>
        {/* <PageTitle>Mesaj Formu</PageTitle>
        <ContactForm /> */}
      </Container>
    </Layout>
  )
}

export default Contact
